p',{staticClass:"small_title"},[_vm._v("奖助学金")]),_c('p',[_vm._v("各高校为成绩优异的考生提供丰厚的奖学金。值得注意的是能拿到全额奖学金的是很小一部分考生，绝大多数考生需要自费。自费生入读后虽然可申请各类奖助学金，但竞争非常激烈。")]),_c('p',{staticClass:"small_title"},[_vm._v("签证")]),_c('p',[_vm._v("一般情况下，申请学生签证的时间为4-6周。内地同学获录取后，应立即着手办理申请香港、澳门特区入境签证手续。学校会在发出录取通知书的同时，把香港、澳门特区入境签证申请表格一同寄给考生。")])]),_c('div',{staticClass:"ques"},[_c('p',{staticClass:"big_title"},[_vm._v("（二）考生申请香港高校从哪些渠道获取信息？")]),_c('p',[_vm._v("在高校招生录取季，学生和家长们还需特别警惕虚假学校。香港也曾出现过“野鸡大学”。如2015年被曝光的“香港苏文大学”，在其网站上以合成照片介绍校园，甚至依靠网络购物平台兜售学历证书。香港特区政府教育局明确表示，“香港苏文大学”并非根据当地《教育条例》注册或临时注册的学校。")]),_c('p',[_vm._v("怎么避免碰上“野鸡大学”呢?香港特区政府教育局在其官网上罗列了香港21所可颁授学位的高等教育院校名称，并提供各院校网站入口。此外，根据教育局官网资料介绍，“香港升学”网站可为非香港本地学生赴港升学提供较全面的资讯，不仅可查询院校、课程等信息，还能为学生在港生活、就业等给予提示。")])]),_c('div',{staticClass:"ques"},[_c('p',{staticClass:"big_title"},[_vm._v("（三）考生能被港校和内地高校同时录取吗？")]),_c('p',[_vm._v("不会存在同时录取的现象。")]),_c('p',[_vm._v("香港大学、香港科技大学、香港理工大学、香港浸会大学、岭南大学、香港教育学院、香港树仁大学、香港公开大学、香港演艺学院、珠海学院等高校每年参考考生高考成绩，在内地自主招生，于7月7日前结束录取，被上述香港高校录取的学生，内地高校不再予以录取。")]),_c('p',[_vm._v("香港中文大学、香港城市大学列入提前录取批次。这意味着考生在填报这两个高校中的任何一所高校同时，也可填报其他本科第一批次的内地重点高校，即便在提前批次未被录取，仍有机会在本科第一批次录取时进入其他的内地重点高校，风险大大降低。")])]),_c('div',{staticClass:"ques"},[_c('p',{staticClass:"big_title"},[_vm._v("（四）考生能被澳门高校和内地高校同时录取吗？")]),_c('p',[_vm._v("可以。澳门高校属于单独招生，如果考生同时被澳门高校与内地高校录取，考生可自行决定在澳门还是在内地就读。")])]),_c('div',{staticClass:"ques"},[_c('p',{staticClass:"big_title"},[_vm._v("（五）港澳高校是否有奖学金？")]),_c('p',[_vm._v("香港高等院校奖学金情况")]),_c('p',[_vm._v("香港高等院校奖学金设置情况，以香港大学和香港中文大学为例。")]),_c('p',[_vm._v("香港大学设立了丰厚的入学奖学金，奖励录取名单中部分成绩特别突出且综合素质优秀的内地学生，全额奖学金将涵盖学生在港大就读期间全部的学费及基本生活费。奖学金无需单独申请，获得港大录取的学生将自动被纳入奖学金考虑之列。此外，香港大学还设有众多不同类别的奖学金及奖项供所有港大在校本科生申请。")]),_c('p',[_vm._v("香港中文大学奖学金有：全额奖学金、全免学费奖学金及本科入学奖学金三种。“全额奖学金”学生将获发相等于攻读四年本科课程的学费，以及每年45000港元住宿及生活津贴；“全免学费奖学金”学生可获发相等于攻读四年本科课程的学费；“本科入学奖学金”学生可获发相等于攻读四年本科课程的一半学费及一万港元生活津贴，四年共三十万港元。")]),_c('p',[_vm._v("香港高校奖学金的设置详情可向各校咨询。")]),_c('p',[_vm._v("澳门高等院校均设有奖学金")]),_c('p',[_vm._v("澳门高等院校均设有奖学金，如澳门大学有全免奖学金和澳门基金会奖学金。")]),_c('p',[_vm._v("澳大每年特设“澳门大学全免奖学金”，获奖学金的考